@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onLaptop {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin onDesign {
  @media (min-width: 1600px) {
    @content;
  }
}

@mixin onFullHD {
  @media (min-width: 1920px) {
    @content;
  }
}

@mixin onFullHD {
  @media (min-width: 2560px) {
    @content;
  }
}
